*{
  border: 0;margin: 0;padding: 0;
  color: inherit;text-decoration: none;list-style-type: none;position: relative;-webkit-box-sizing: border-box;box-sizing: border-box;}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
button, input, a{
  outline: none;
}
input{
  width: 286px;
    padding: 18px 20px;
    border-bottom: 1px solid #c1c1c1;
}
.btn{
  background-color: #e74c2c;
  color: #ffffff;
  font-size: 15px;
  transition: 0.4s;
  padding: 14px 60px;
  text-transform: uppercase;
  margin-top: 20px;
}
.btn:hover{
 background-color: rgb(45, 45, 45);
 border-color: rgb(45, 45, 45);
}
/* Componente CampoProducto */
.input-generic{
  width: 250px;
  padding: 18px 15px;
  border-bottom: 1px solid #c1c1c1;
  border-radius: 10px;
}

.cont-formulario{
  background-image: url(http://www.ismedioambiente.com/wp-content/uploads/2011/06/Convenio-Europeo-del-Paisaje-560x420.jpg);
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
}
.cont-formulario::before{
  content: "";
    background-color: rgba(175, 55, 0, 0.86);
    background-size: cover;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.cont-logo{
  width: 89%;
    height: 204px;
    background-color: #e74c2c;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: -111px;
    margin-bottom: 54px;
    box-shadow: -2px 3px 9px -4px black;
}
.cont-logo img{}
.cont-logo p{
  text-transform: uppercase;

}

.form-login{
  width: 420px;
  height: 480px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
